import { compute, state } from "./belling";
import { div, ForEach, p, root, textarea } from "./belling-dom";

function main() {
  const text = state("");
  return div(
    textarea().on({
      input(e) {
        text.v = e.target.value;
      },
    }),
    ForEach(
      "div",
      compute(() => text.v.split("\n")),
      (line) => {
        return p(line);
      }
    )
  );
}
new root(document.body, main());
